<template>
  <div v-if="!uploaded" class="container">
    <div class="title">
      <h1> Upload csv file of scanned devices: </h1>
    </div>
    <div class="form-outline">
      <label class="label">
        <input
          type="file"
          id="file"
          ref="file"
          accept=".csv"
          v-on:change="handleFileUpload()"
        />
      </label>
      <mdb-btn
        class="button"
        color="primary"
        v-on:click="
          submitFile();
        "
        >Submit</mdb-btn
      >
    </div>
  </div>
  <div v-else>
    <div class="uploaded">
      <h1 class="green-text"> Uploaded! </h1>
    </div>
  </div>
</template>

<script>
import { mdbBtn } from "mdbvue";
import router from '../router/index.js';

export default {
  components: {
    mdbBtn,
  },
  data() {
    return {
      file: "",
      csv: "",
      devices: [],
      types: [],
      tvs: [],
      uploaded: false
    };
  },
  mounted() {

  },
  methods: {
    handleFileUpload() {
      this.file = this.$refs.file.files[0];
    },
    submitFile() {
      const reader = new FileReader();
      reader.onload = (e) => {
        console.log('submitFile started');
        this.devices = [];

        this.csv = e.target.result;
        var arr = this.csv.split("\n");

        for (var i = 2; i < arr.length; i++) {
          var tmp = arr[i].split(",");

          var obj = {
            id: i - 2,
            mac: tmp[0],
            ssid: tmp[1],
            authMode: tmp[2],
            firstSeen: tmp[3],
            channel: tmp[4],
            rssi: tmp[5],
            currentLatitude: tmp[6],
            currentLongitude: tmp[7],
            altitudeMeters: tmp[8],
            accuracyMeters: tmp[9],
            type: tmp[10],
          };

          this.devices.push(obj);
        }
        this.saveDevices();
        console.log('submitFile done');
      };
      reader.readAsText(this.file);
    },
    saveDevices() {
      console.log('saveDevices started');
      // devices
      localStorage.removeItem("devices");
      const parsedDevices = JSON.stringify(this.devices);
      try {
        localStorage.setItem("devices", parsedDevices);
      } catch (e) {
        console.log(e);
      }

      // types
      this.types = this.getTypes();
      localStorage.removeItem("types");
      const parsedTypes = JSON.stringify(this.types);
      try {
        localStorage.setItem("types", parsedTypes);
      } catch (e) {
        console.log(e);
      }

      // tvs
      this.tvs = this.getTVs();
      localStorage.removeItem("tvs");
      const parsedTvs = JSON.stringify(this.tvs);
      try {
        localStorage.setItem("tvs", parsedTvs);
      } catch (e) {
        console.log(e);
      }

      this.uploaded = true;
      console.log('saveDevices done');

    },
    goToDevices() {
      router.push('Dashboard');
    },
    getTypes() {
      var count = [0,0,0,0];
      var i;
      for(i = 0; i < this.devices.length; i++) {
        switch(this.devices[i].type) {
          case 'WIFI':
            count[0]++;
            break;
          case 'BT':
            count[1]++;
            break;
          case 'BLE':
            count[2]++;
            break;
          default:
            count[3]++;
        }
      }
      return count;
    },
    getTVs() {
      var tmp = [];
      var i;
      for(i = 0; i < this.devices.length; i++) {
        if(this.devices[i] && this.devices[i].ssid && this.devices[i].ssid.includes('TV')) {
          tmp.push(this.devices[i]);
        }
      }
      return tmp;
    },
  },
  computed: {
    headers() {
      return [{ text: "Name", value: "name" }];
    },
  },
};
</script>

