<script>
import PieChart from "./charts/PieChart.js";
import SimpleMap from "./SimpleMap.vue"

export default {
  components: {
    PieChart,
    SimpleMap
  },
  data() {
    return {
      isMounted: false,
      devices: [],
      types: [],
      tvs: [],
      chartData: {
        hoverBackgroundColor: "red",
        hoverBorderWidth: 10,
        labels: ["WIFI", "BT", "BLE", "Other"],
        datasets: [
          {
            label: "Devices",
            backgroundColor: ["#2E7D32", "#1565C0", "#283593", "#C62828"],
            data: [0,0,0,0]
          }
        ]
      }
    }
  },
  mounted() {
    if (localStorage.getItem("devices")) {
      try {
        this.devices = JSON.parse(localStorage.getItem("devices"));
        this.types = JSON.parse(localStorage.getItem("types"));
        this.tvs = JSON.parse(localStorage.getItem("tvs"));
        this.chartData.datasets[0].data = this.types;
        this.isMounted = true;

      } catch (e) {
        console.log(e)
        localStorage.removeItem("devices");
      }
    }
  },
  methods: {
    csvExists() {
      return localStorage.getItem("devices");
    }
  }
}
</script>

